body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .dashboard-container {
    display: flex;
    flex-direction: column;
    font-size: 14px;
  }
  
  /* Header */
  .header {
    height: 70px;
    background-color: #003366;
    color: white;
    display: flex;
    align-items: center;
    padding-left: 20px;
  }
  
  .greeting {
    margin: 10px 220px;
    font-size: 1.2em;
  }
  
  /* Sidebar */
  .sidebar {
    width: 200px;
    background-color: #f0f0f0;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    padding-top: 80px;
  }
  
  .sidebar ul {
    list-style: none;
    padding: 0;
  }
  
  .sidebar li {
    display: flex;
    align-items: center;
    padding: 15px 20px;
    font-size: 1.1em;
    color: #797979;
    transition: background 0.3s;
  }
  .sidebar li:hover {
    background-color: #e0e0e0;
    cursor: pointer;
}
  
  .sidebar-icon {
    width: 24px;
    height: 24px;
    margin-right: 10px;
  }
  
  /* Statistics Section */
  .statistics-container {
    display: flex;
    justify-content: space-between;
    margin: 20px 220px;
  }
  
  .stat-card {
    width: 180px;
    background-color: #f9f9f9;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  .stat-card h1{
    font-size: 1.0em;
  }
  .stat-card h2{
    margin-top: 20px;

  }

  
  /* Announcement Section */
  .announcement-container {
    margin: 20px 220px;
    width: calc(100% - 440px);
  }
  
  #post-text {
    width: 100%;
    height: 80px;
    border-radius: 5px;
    padding: 10px;
    resize: none;
  }
  
  .divider {
    margin: 10px 0;
  }
  
  .file-label {
    display: inline-flex;
    align-items: center;
    margin-right: 10px;
    cursor: pointer;
  }
  
  #file-chosen {
    margin-left: 5px;
  }
  
  /* Content Section */
  .content-container {
    display: flex;
    justify-content: space-between;
    margin: 20px 220px;
  }
  
  .student-attendance, .modules-container {
    width: 300px;
    padding: 20px;
    border-radius: 10px;
    background-color: #f9f9f9;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .graph {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    height: 150px;
  }
  
  .bar {
    width: 20%;
    background-color: #003366;
  }
  
  .module {
    background-color: white;
    padding: 10px;
    margin: 5px 0;
    border-radius: 5px;
  }
  