body {
    font-family: Arial, sans-serif;
    background-color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
}

.header {
    width: 100%;
    background-color: #002f87;
    padding: 10px;
    color: white;
    text-align: left;
    position: fixed;
    top: 0;
    left: 0;
}

.header h1 {
    margin: 0;
    font-size: 1.5em; /* Adjusted for better readability */
}

.Signup-container {
    background-color: #f9f9f9;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(114, 98, 98, 0.1);
    text-align: center;
    margin-top: 10%;
    margin-left: 10%; /* Adjusted margin to center the form */
    margin-right: 10%; /* Adjusted margin to center the form */
}

.Signup-container h2 {
    background-color: #002f87;
    color: white;
    margin: 0 -30px 20px -30px;
    padding: 15px;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}

.Signup-container form input[type="text"],
.Signup-container form input[type="email"],
.Signup-container form input[type="password"],
.Signup-container form input[type="number"],
.Signup-container form input[type="date"] {
    width: 70%;
    padding: 10px;
    margin: 2% 0 1% 0; /* Adjusted margins */
    border: none;
    border-bottom: 2px solid #ccc;
    background-color: transparent;
    outline: none;
    font-size: 16px;
    display: grid;
}

.Signup-container input:focus {
    border-bottom: 2px solid #002f87;
}

.Signup-container a {
    display: block;
    margin-top: 1%;
    color: #002f87;
    text-decoration: none;
}

.Signup-container a:hover {
    text-decoration: underline;
}

.error {
    color: red;
    margin-bottom: 1%;
}

.Signup-container form {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 1.5%;
    grid-template-rows: auto auto auto; /* Added to define rows */
}

.gender {
    display: flex;
    align-items: center;
}

.gender label {
    margin-right: 1%;
}

form input[type="submit"] {
    grid-column: span 4; /* Span across all columns */
    padding: 12px;
    background-color: #1e3a8a;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 100%;
    cursor: pointer;
    margin-top: 2%; /* Added margin to move it down */
}

form input[type="submit"]:hover {
    background-color: #17408b;
}

/* Sign-in Link */
form a {
    grid-column: span 4; /* Span across all columns */
    margin-top: 2%;
    color: #1e3a8a;
    text-decoration: none;
}

form a:hover {
    text-decoration: underline;
}

/* Media Queries for responsiveness */
@media (max-width: 768px) {
    .Signup-container {
        width: 100%;
        margin-top: 10%;
    margin-left: 10%; /* Adjusted margin to center the form */
    margin-right: 10%; 
    }

    form {
        grid-template-columns: 1fr; /* Single column layout */
    }

    form input[type="text"],
    form input[type="email"],
    form input[type="password"],
    form input[type="number"],
    form input[type="date"] {
        width: 100%;
    }

    form input[type="submit"],
    form a {
        grid-column: span 1; /* Span across single column */
    }
}

@media (max-width: 480px) {
    .Signup-container {
        width: 90%;
        padding: 20px;
    }

    .Signup-container h2 {
        margin: 0 -20px 20px -20px;
        padding: 10px;
    }

    form input[type="text"],
    form input[type="email"],
    form input[type="password"],
    form input[type="number"],
    form input[type="date"] {
        width: 70%;
    padding: 10px;
    margin: 3% 3% 3% 3%; /* Adjusted margins */
    border: none;
    border-bottom: 2px solid #ccc;
    background-color: transparent;
    outline: none;
    font-size: 16px;
    display: grid;
    }

    form input[type="submit"] {
        padding: 10px;
        font-size: 90%;
    }
}

@media (max-width: 360px) {
    .Signup-container {
        width: 95%;
        padding: 15px;
    }

    .Signup-container h2 {
        margin: 0 -15px 15px -15px;
        padding: 8px;
        font-size: 18px;
    }

    form input[type="text"],
    form input[type="email"],
    form input[type="password"],
    form input[type="number"],
    form input[type="date"] {
        font-size: 12px;
    }

    form input[type="submit"] {
        padding: 8px;
        font-size: 85%;
    }
}
