        body {
            font-family: Arial, sans-serif;
            margin: 0;
            padding: 0;
            background-color: #f4f4f4;
        }

        .sidebar {
            width: 10%;
            background-color: #F4F4F4;
            position: fixed;
            height: 100%;
            color: black;
            top: 0;
            left: 0;
            margin-top: 3.5%;
            padding-top: 20px;
            text-align: center; /* Center align text and images */
        }

  .sidebar ul {
    list-style-type: none;
    padding: 0;
  }

  .sidebar ul li {
    padding: 20px;
    text-align: center;
  }

  .sidebar ul li a {
    text-decoration: none;
    color: black;
    font-size: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .header {
            width: 100%;
            background-color: #002f87;
            padding: 10px;
            color: white;
            text-align: left;
            position: fixed;
            top: 0;
            left: 0;
        }

        .header h1 {
            margin: 0;
            font-size: 1.5em; /* Adjusted for better readability */
            display: inline-block; /* Added to keep the title inline */
        }

        .header img {
            float: right; /* Float images to the right */
            margin-left: 1%; 
            margin-right: 2%;/* Add some space between images */
            width: 2%;
            height: 2%;
        }


        .settings-section {
            display: flex;
            flex-direction: column;
            gap: 20px;
            
        }

        .sec-box {
            background-color: white;
            padding: 20px;
            border: 1px solid #ddd;
            border-radius: 8px;
            box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
        }

        .sec-box h2 {
            font-size: 22px;
            margin-bottom: 20px;
            color: #333;
        }

        .sec-box .row {
            display: flex;
            align-items: center;
            gap: 20px;
            margin-bottom: 10px;
        }

        .row label {
            flex-basis: 25%;
            font-size: 16px;
            color: #666;
        }

        .row input {
            flex-grow: 1;
            padding: 10px;
            border-radius: 5px;
            border: 1px solid #ccc;
            font-size: 16px;
        }

        .profile-image {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 20px;
            background-color: #f4f4f4;
            border-radius: 50%;
            width: 100px;
            height: 100px;
            margin-bottom: 20px;
        }
        .sidebar img {
            display: block;
            margin: 0 auto; /* Center images */
        }