body {
    font-family: Arial, sans-serif;
    background-color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
}

/* Full-width blue header */
.header {
    width: 100%;
    background-color: #002f87;
    padding: 10px;
    color: white;
    text-align: left;
    position: fixed;
    top: 0;
    left: 0;
}

.header h1 {
    margin: 0;
    font-size: 1.5em; /* Adjusted for better readability */
}
.forgot-container {
    background-color: #f9f9f9;
    padding: 40px;
    border-radius: 8px;
    width: 300px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    margin-top: 100px; /* Adjusted margin to make space for the header */
}

.forgot-container h2 {
    background-color: #002f87;
    color: white;
    margin: 0 -40px 20px -40px;
    padding: 15px;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}

.forgot-container input {
    width: 100%;
    padding: 10px;
    margin: 20px 0 10px 0;
    border: none;
    border-bottom: 2px solid #ccc;
    background-color: transparent;
    outline: none;
    font-size: 16px;
}

.forgot-container input:focus {
    border-bottom: 2px solid #002f87;
}

.code-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.code-container input {
    width: calc(100% - 90px);
}

.send-code-btn {
    padding: 10px;
    width: 70px;
    background-color: #002f87;
    border: none;
    color: #fff;
    border-radius: 4px;
    cursor: pointer;
}

.send-code-btn:hover {
    background-color: #0052cc;
}

.confirm-btn {
    width: 100%;
    padding: 10px;
    background-color: #002f87;
    border: none;
    color: #fff;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 20px;
}

.confirm-btn:hover {
    background-color: #0052cc;
}

.error {
    color: red;
    margin-bottom: 10px;
}

/* Media Queries for responsiveness */
@media (max-width: 768px) {
    .forgot-container {
        width: 80%;
    }
}

@media (max-width: 480px) {
    .forgot-container {
        width: 90%;
        margin-top: 60px;
        padding: 20px;
    }

    .forgot-container h2 {
        margin: 0 -20px 15px -20px;
        padding: 10px;
    }
}
